
// 匹配目标格式粘贴
const MATCH_FORMAT = 'matchFormat';

// 保留源格式粘贴
const SOURCE_FROMAT = 'sourceFormat';

// 纯文本格式粘贴
const TEXT_FORMAT = 'textFormat';

const CARET_HEIGHT = 20;

const PASTE_DECORATION = 'paste-decoration';

const ICON_CHECK_STYLE = {
  fontSize: 12,
  color: '#007AFF',
};
const ICON_PASTE_STYLE = {
  fontSize: 18,
  color: '#111F2C',
  opacity: 0.72,
};

const DROPDOWN_STYLE = {
  borderRadius: 4,
  border: '0.5px solid rgba(25,31,37,0.08)',
  boxShadow: '0 10px 20px 0 rgba(0,0,0,0.2)',
  fontSize: '14px',
};

const SPLIT_BUTTON_STYLE = { width: '100%', height: '100%'};

const PASTE_MODES = {
  sourceFormat: SOURCE_FROMAT,
  matchFormat: MATCH_FORMAT,
  textFormat: TEXT_FORMAT,
  markdownToDoc: 'markdownToDoc',
};

const DEFAULT_PASTE_MODES = [
  PASTE_MODES.sourceFormat,
  PASTE_MODES.matchFormat,
  PASTE_MODES.textFormat,
];

enum ClipboardErrorEnum {
  COPY_FAILED = 'COPY_FAILED', // 复制失败
  CUT_FAILED = 'CUT_FAILED', // 剪切失败
  PASTE_FAILED = 'PASTE_FAILED', // 粘贴失败
  PASTE_TIME_OUT = 'PASTE_TIME_OUT', // 粘贴超时
}
/**
 *  1. 首字母为 # 开头，
 *  2. 首字母为 * + 空格
 *  3. 首字母为 --- + \n
 *  4. 首字母为 数字 + . + 空格
 *  满足以上任一情况，则识别为 markdown 语法
 * */
const MARKDOWN_GRAMMER_REGEXP = /^#|^(\* )|^(\- )|^(\-{3}\n|^(\d+\. ))/;

const FILE_IMG_TYPE = 'image/png';

export {
  CARET_HEIGHT,
  DROPDOWN_STYLE,
  MATCH_FORMAT,
  ICON_CHECK_STYLE,
  ICON_PASTE_STYLE,
  DEFAULT_PASTE_MODES,
  PASTE_DECORATION,
  SOURCE_FROMAT,
  SPLIT_BUTTON_STYLE,
  TEXT_FORMAT,
  PASTE_MODES,
  MARKDOWN_GRAMMER_REGEXP,
  FILE_IMG_TYPE,
  ClipboardErrorEnum,
};
